let host = ''
let apiUrl = ''

if (process.env.NODE_ENV === 'development') {
    // 开发环境
    // host = 'http://www.hyc.com/xcx'
    host = 'http://yang.park666.com'
    apiUrl = host + '/api.html'
    
} else if (process.env.NODE_ENV === 'production') {
    // 生产环境
    host = window.host
    apiUrl = host + '/api.html'
}

export {
    host,
    apiUrl
}



